.has-horizontal-scrolling {
  width: 100%;
  overflow-x: scroll;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  scrollbar-width: none;
  scroll-snap-type: x mandatory;
  
  & > * {
    scroll-snap-align: start;
    flex: 0 0 20%;
    
    text-align: center;
  }

  &::-webkit-scrollbar {
    display: none;
  }
}